 * @Author: lmx
 * @Date: 2020-06-01 09:47:29
 * @LastEditTime: 2020-09-29 13:32:29
 * @LastEditors: Please set LastEditors
 * @Description: 卡片切换效果
 * @FilePath: \aiku\examples\components\doc\doc-src\card-tabs\card-tabs.vue
--> 
<style lang='less' scoped>
    @import './card-tabs.less';
</style>

<template>
    <div class="card-tabs" :style="{perspective: perspectiveWidth * 6 + 'px', padding: perspectiveHeight/10 + 'px 0'}"> 
        <div @click="leftTog" class="left-icon">
            <slot v-if="showCustomLeftIcon" name="leftIcon"></slot>
            <Icon v-else type="md-arrow-dropleft-circle" />
        </div>
        <div ref="modules"
            class="modules" 
            :style="{'transform': 'rotateY('+ ((-viewIndex + 1) * rotateNum) +'deg)'}">
            <transition-group name="list" tag="p">
                <div :style="{transform: 'rotateY('+( rotateNum * (index - 1) )+'deg) translateZ('+ perspectiveWidth *.9 +'px)'}" 
                    v-show="index == viewIndex || index == viewIndex + 1 || index == viewIndex - 1" 
                    v-for="(item,index) in cards" 
                    :key="item.slotName" 
                    class="module">
                    <slot :name="item.slotName"></slot>
                </div>
            </transition-group>
        </div>
        <div @click="rightTog" class="right-icon">    
            <slot v-if="showCustomRightIcon" name="rightIcon"></slot>
            <Icon v-else type="md-arrow-dropright-circle" />
        </div>
    </div>
</template>

<script>
export default {
    name: 'care-tabs',
    props: {
        // 卡片列表
        cards: {
            type: Array,
            default() {
                return [];
            }
        },
        // 是否最多限制3个
        isMax: {
            type: Boolean,
            default() {
                return true;
            }
        }
    },
    data() {
        return {
            // 视图宽度
            perspectiveWidth: 0,
            // 视图高度
            perspectiveHeight: 0,
            // 当前视图索引
            viewIndex: 1,
            // 旋转角度
            rotateNum: 60
        }
    },
    computed: {
        // 是否显示自定义左侧图标
        showCustomLeftIcon() {
            return this.$slots.leftIcon;
        },
        // 是否显示自定义右侧图标
        showCustomRightIcon() {
            return this.$slots.rightIcon;
        }
    },
    methods: {
        // 获取视图宽度
        getPresWidth() {
            this.$nextTick(() => {
                setTimeout(() => {
                    this.perspectiveWidth = this.$refs.modules.clientWidth;
                }, 300)
            })
            window.onresize = ()=>{
                this.$nextTick(() => {
                    this.perspectiveWidth = this.$refs.modules.clientWidth;
                })
            }
        },
        // 获取视图高度
        getPresHeight() {
            this.$nextTick(() => {
                setTimeout(() => {
                    this.perspectiveHeight = this.$refs.modules.clientHeight;
                }, 300)
            })
            window.onresize = ()=>{
                this.$nextTick(() => {
                    this.perspectiveHeight = this.$refs.modules.clientHeight;
                })
            }
        },
        // 左切换
        leftTog() {
            if (this.viewIndex > 0) {
                this.viewIndex -= 1;
            }
        },
        // 右切换
        rightTog() {
            if (this.viewIndex < this.cards.length - 1) {
                this.viewIndex += 1;
            }
        },
        // 设置视图索引
        setViewIndex(index) {
            if (index >= 0 && index <= this.cards.length - 1) {
                this.viewIndex = index;
            } else {
                console.log('设置的视图索引为空');
            }
        },
        // 确认默认值
        rectDefaults() {
            if (!this.isMax) {
                this.rotateNum = 360 / this.cards.length;
            }
        }
    },
    created() {
        this.rectDefaults();
    },
    mounted() {
        this.getPresWidth();
        this.getPresHeight();
    },
    watch: {
        viewIndex(val) {
            this.$emit('on-toggle', val);
        }
    }
}
</script>
